*, 
*::after, 
*::before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
}

html
{
  color: @color-gray-ddd;
  .mixin_font-md;
}

html,
input, 
button {
  font-family: 'VarelaRound', sans-serif, Arial, 'Times New Roman', Times, serif, 'PingFang TC', 'Microsoft JhengHei';
}

pre {
  font-family: 'Ubuntu', sans-serif, Arial, 'Times New Roman', Times, serif, 'PingFang TC', 'Microsoft JhengHei';
}

html,
body {
  height: 100%;
}

body, 
div, 
p, 
ul {
  margin: 0;
  padding: 0;
}

a {
  &.inline-link {
    .mixin_margin-md-l;
  }

  &:link,
  &:visited {
    color: @color-gray;
  }

  &:hover {
    color: @color-primary;
  }
}